* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

header {
  display: flex;
  justify-content: space-between;
  margin: 1.5rem 0.5rem;
}
header div.color {
  text-align: center;
}
header div.color input[type=color] {
  -webkit-appearance: none;
  border: 5px solid white;
  border-radius: 50%;
  cursor: pointer;
  width: 40px;
  height: 40px;
}
header div.color input[type=color]::-webkit-color-swatch-wrapper {
  padding: 0;
}
header div.color input[type=color]::-webkit-color-swatch {
  border: none;
  border-radius: 50%;
}
header div.color input[type=color]:hover {
  transition: all 0.2s ease;
  transform: scale(1.3);
  border: 5px solid yellow;
  box-shadow: 0 0 10px yellow;
}
header div.color p.changeColor {
  margin: 0.3rem;
  color: white;
  font-weight: bold;
}
header h1.title {
  color: white;
  text-shadow: 3px 3px 5px red;
  font-size: 2.5rem;
  font-family: "SEGA LOGO FONT";
  font-weight: 100;
}
@font-face {
  font-family: "SEGA LOGO FONT";
  font-style: normal;
  font-weight: normal;
  src: local("SEGA LOGO FONT"), url("./font-face/SEGA.woff") format("woff");
}

.cardList {
  display: flex;
  flex-wrap: wrap;
}
.cardList .card {
  border: 5px solid white;
  border-radius: 20px;
  margin: 0.5rem;
  text-align: center;
}
.cardList .card .roboInfo {
  background: gold;
  padding: 0.5rem;
  border-radius: 0 0 15px 15px;
}

div.search {
  display: flex;
  justify-content: center;
}
div.search input[type=search] {
  font-size: 1.5rem;
  padding: 0.5rem;
  margin-bottom: 1rem;
}
div.search input[type=search]:focus {
  outline: 3px solid red;
  box-shadow: 0 0 15px yellow;
}/*# sourceMappingURL=style.css.map */